import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AuthService } from '../auth/auth.service';
import { ToastrService } from 'ngx-toastr';
import { tap, catchError, map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { throwError } from 'rxjs';


@Injectable()
export class CustomHttpClient {
	private httpOptions;
	private logsReqResData = true; // Make it True For Debugging purpose only
	private _baseApiUrl = environment.baseApiUrl;

	constructor(private newHttp: HttpClient, private authService: AuthService, private toastr: ToastrService, private router: Router) { }

	private _configHeaders = () => {
		let headers = new HttpHeaders();
		headers = headers.set('Content-Type', 'application/json');
		// 'Content-Type': 'application/json',
		// 'Cache-Control': 'no-cache, no-store',
		if (this.authService.getStoredUser()) {
			headers = headers.set('Authorization', 'Bearer ' + this.authService.user.token);
		}
		this.httpOptions = { headers };
	};

	get(url, showToastr = false) {
		this._configHeaders();
		return this.newHttp.get(this._baseApiUrl + url, this.httpOptions).pipe(
			tap((resp: any) => {
				if (resp.status === 0 && resp.error.httpStatus === 503) {
					alert('Session Expired');
					this.authService.removeUser();
					this.router.navigate(['mobile/login']);
				}
				if (resp.status && showToastr) {
					this.showToastr('success', resp.message, '');
				}
				this.consoleLogData({ method: 'POST', url, resp });
			}),
			catchError(err => {
				this.handleError(err, showToastr);
				this.consoleLogData(err);
				return throwError(err);
			})
		);
	}

	post(url, data, showToastr = false) {
		this._configHeaders();
		return this.newHttp.post(this._baseApiUrl + url, data, this.httpOptions).pipe(
			tap((resp: any) => {
				console.log('--------------->', resp);
				if (resp.status === 0 && resp.error.httpStatus === 503) {
					alert('Session Expired');
					this.authService.removeUser();
					this.router.navigate(['mobile/login']);
				}
				if (resp.status && showToastr) {
					this.showToastr('success', resp.message, '');
				}
				// this.consoleLogData({ method: 'POST', url, data, resp });
			}),
			catchError(err => {
				this.handleError(err, showToastr);
				this.consoleLogData(err);
				return throwError(err);
			})
		);
	}

	handleError(error, showToastr) {
		if (showToastr) {
			this.showToastr('error', error.message, error.status);
		}
		if (error.status === 401) {
			this.authService.removeUser();
			this.router.navigate(['/mobile/login']);
		} else if (error.status === 500) {
			// this.router.navigate(['/dashboard/server-error']);
		}
	}

	consoleLogData(data) {
		if (this.logsReqResData) {
			console.log('------ API LOGS -----', data);
		}
	}

	showToastr(type, msg, title) {
		this.toastr[type](msg, title, {
			progressBar: true,
			progressAnimation: 'increasing',
			timeOut: 1500,
			positionClass: 'toast-bottom-right' // There is no interaction button/input
		});
	}
}
